.footer-input-form {
    position: relative;
    top: 10%;
    left: 19%;
  }
  
  button {
    display: block;
    font-size: 1.1rem;
    font-weight: 500;
    padding: 12px 55px;
    width: 62%;
    margin-top: 20px;
    border: none;
    outline: none;
    background-color: #000;
    color: #fff;
    cursor: pointer;
    border-radius: 20px;
  }
  
  .footer-input-form .input-1 {
    display: block;
    font-size: 1.1rem;
    font-weight: 500;
    padding: 8px 8px;
    outline: none;
    border: 2px solid rgb(122, 122, 247);
  
    border: 2px solid #b4f2f3;
  }
  
  .footer-input-form .input-2 {
    display: block;
    font-size: 1.1rem;
    font-weight: 500;
    border: 1px solid rgb(214, 214, 214);
    padding: 8px 8px;
    text-align: center;
    border-radius: 25px;
    margin-top: 20px;
    outline: none;
  }
  
  .input-2::placeholder {
    color: #333;
    position: relative;
    right: 5px;
  }
  
  .input-1::placeholder {
    position: relative;
    right: 5px;
  }
  
  .footer-input-form span {
    text-align: center;
    position: initial;
    top: 35px;
    left: 35px;
    font-size: 1rem;
    color: rgb(65, 63, 63);
  }
  
  .footer-input-form span a {
    color: #00f;
    font-weight: 600;
  }
  
